export default {
  props: {
    show: {
      type: Boolean
    },
    size: {
      type: String
    }
  },
  data () {
    return {
      isShow: Boolean
    }
  },
  created () {
    this.isShow = this.show
  },
  watch: {
    show () {
      this.isShow = this.show
      if (!this.show) {
        this.close()
      }
    }
  },
  methods: {
    close () {
      this.isShow = false
      setTimeout(() => {
        this.$emit('close')
      }, 200)
    }
  }
}

